import './vendor';
require ('./vendor/slick.min.js');
require ('./vendor/jquery.validate.min.js');
require ('./vendor/jquery.inputmask.min.js');
require ('./function/yandex.js');

$(document).ready(function () {
   require ('./function/search.js');
   require ('./function/sliders.js');
   require ('./function/offers.js');
   require ('./function/fixed-menu.js');
   require ('./function/hover-nav-menu.js');
   require ('./function/modal.js');
   valid('#js-form-user');
   valid('#js-form-password');
   valid('#js-form-registration');
});
function valid(form) {
   $.validator.addMethod("minlenghtphone", function (value, element) {

          return value.replace(/\D+/g, '').length > 10;
       },
       "");
   $.validator.addMethod("requiredphone", function (value, element) {

          return value.replace(/\D+/g, '').length > 1;
       },
       "");
   $(form).validate({
      rules: {
         name: {
            required: true,
            minlength: 2
         },
         email: {
           required: true,
           email: true
         },
         phone: {
            requiredphone: true,
            minlenghtphone: true
         },
         terms: {
            required: true
         }
      },
      messages: {
         name: {
            required: ''
         },
         phone: {
            required: ''
         },
         terms: {
            required: ''
         }
      },
      errorPlacement: function(error, element) {},
      highlight: function(element, errorClass, validClass) {
      },
      unhighlight: function(element, errorClass, validClass) {
      },
      submitHandler: function submitHandler(form) {
         //alert("Submitted!");
         $.ajax({
            type: $(form).attr('method'),
            url: $(form).attr('action'),
            data: $(form).serialize(),
            success: function (data) {
               if(parseInt(data) == 1) {
                  $(form).removeClass('form--active');
                  $(form).siblings('.form__ok').addClass('form--active');
               } else {
                  $(form).removeClass('form--active');
                  $(form).siblings('.form__ok').addClass('form--active');
               }
            },
            error: function () {
               $(form).removeClass('form--active');
               $(form).siblings('.form__ok').addClass('form--active');
            }
         });

         return false;
      }
   });
}

$(function () {
   $("#js-form-phone").inputmask("+ 7 (999) 999 99 99", {
      placeholder: "_"
   });
});